import React, { useState } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useLocation,
  useNavigate,
} from "react-router-dom";
import { Provider, useDispatch, useSelector } from "react-redux";
import store, { RootState } from "../../redux/store";
import GlobalMenu from "../../components/layout/GlobalMenu";
import Home from "../../pages/home/Home";
import {
  setUserProfile,
  clearUserProfile,
  loginStart,
  loginSuccess,
  loginFailure,
  signupStart,
  signupSuccess,
  signupFailure,
  logout,
} from "../../pages/home/userSlice";
import {
  Container,
  CssBaseline,
  Box,
  Button,
  Typography,
  TextField,
  Link,
} from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import {
  CognitoUserPool,
  CognitoUser,
  AuthenticationDetails,
  CognitoUserSession,
} from "amazon-cognito-identity-js";

// PW
const LoginPwEntry: React.FC = () => {
  // Cognito
  const poolData = {
    UserPoolId: "ap-northeast-1_f2DWq8JMM",
    ClientId: "52raclcpqs9d6skfn49293uv8f",
  };
  const userPool = new CognitoUserPool(poolData);

  // 
  const location = useLocation();
  const dispatch = useDispatch();
  const error = useSelector((state: RootState) => state.user.error);
  const passedEmail = location.state?.email || "";
  const [email, setEmail] = useState(passedEmail);
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  // 
  const handleLogin = async () => {
    console.log("");
    dispatch(loginStart()); // 
    const authenticationDetails = new AuthenticationDetails({
      Username: email,
      Password: password,
    });
    const userData = {
      Username: email,
      Pool: userPool, //  App.tsx 
    };
    const cognitoUser = new CognitoUser(userData);
    console.log("CognitoUser:", cognitoUser);

    // 
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: (result) => {
        console.log(
          ":",
          result.getAccessToken().getJwtToken()
        );
        dispatch(loginSuccess(email)); // email  state 
        navigate("/home");
      },
      onFailure: (err) => {
        console.error(":", err);
        dispatch(loginFailure(err.message || ""));
      },
    });
  };

  return (
    <Box>
      <TextField
        label=""
        fullWidth
        value={email}
        disabled
        sx={{ mb: 2 }}
      />
      <TextField
        label=""
        type="password"
        fullWidth
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        sx={{ mb: 2 }}
      />
      {error && (
        <Typography color="error" sx={{ mt: 2 }}>
          
        </Typography>
      )}
      <Button onClick={handleLogin} variant="contained">
        
      </Button>
    </Box>
  );
};

export default LoginPwEntry;
