import React from "react";
import { Box, Typography } from "@mui/material";
import AgentCard from "../molecules/AgentCard";

// 
interface AgentItem {
  elementId: string;
  title: string;
  description: string;
  image: string;
  detailPage: string; // 
}

// 
interface Props {
  categoryName: string;
  agents: AgentItem[];
}

/**
 * 
 * 
 */
const AgentCategoryContainer: React.FC<Props> = ({ categoryName, agents }) => {
  return (
    <Box sx={{ marginBottom: 4 }}>
      {/*  */}
      <Typography variant="h5" sx={{ backgroundColor: '#E3823D', color: 'white' }}>
        {categoryName}
      </Typography>
      
      {/*  -  */}
      <Box sx={{ display: "flex", flexWrap: "wrap", gap: 2 }}>
        {agents.map((agent, index) => (
          <AgentCard
            elementId={agent.elementId}
            id={index + 1}
            categoryName={categoryName}
            myleName={agent.title}
            description={agent.description}
            image={agent.image}
            navigateTo={`/${agent.detailPage}`} // detailPage
          />
        ))}
      </Box>
    </Box>
  );
};

export default AgentCategoryContainer;