import { createSlice, PayloadAction } from "@reduxjs/toolkit";

// 
interface UserState {
  sub: string | null;
  email?: string | null;
  name?: string | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  [key: string]: any | null; // 
}

const initialState: UserState = {
    sub: null,
    email: null,
    name: null,
    isAuthenticated: false,
    isLoading: false,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    // 
    setUserProfile: (state, action: PayloadAction<UserState>) => {
      state.profile = action.payload;
    },
    // 
    clearUserProfile: (state) => {
      state.profile = null;
    },
    // 
    loginStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    loginSuccess: (state, action: PayloadAction<string>) => {
      state.isLoading = false;
      state.isAuthenticated = true;
      state.email = action.payload;
      state.error = null;
      
    },
    loginFailure: (state, action: PayloadAction<string>) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    // 
    signupStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    signupSuccess: (state) => {
      state.isLoading = false;
    },
    signupFailure: (state, action: PayloadAction<string | null>) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    logout: (state) => {
      state.isAuthenticated = false;
      state.email = null;
      state.error = null;
    },
  },
});

export const { setUserProfile, clearUserProfile, loginStart, loginSuccess, loginFailure, signupStart, signupSuccess, signupFailure, logout} = userSlice.actions;
export default userSlice.reducer;
